import { 
    Divider,
    Form,
    Flex,
    Switch,
    Checkbox,
    Col
} from "antd";

import {
	useState,
	useContext,
	useEffect
} from "react";

import { AppContext } from "../../contexts/FormContext";

const BServicesToShipmentsAddress = () => {


    const data = [
        {name: "Боковая загрузка", value: "0xb83b7589658a3851440a853325d1bf69", alias:"side-loading"},
        {name: "Верхняя загрузка", value: "0xabb9c63c596b08f94c3664c930e77778", alias:"high-loading"},
        {name: "Гидроборт", value: "0x92fce2284f000b0241dad7c2e88b1655", alias:"tial-lift"},
        {name: "Манипулятор", value: "0x88f93a2c37f106d94ff9f7ada8efe886", alias:"manipulator"},
        {name: "Требуется открытая машина", value: "0x9951e0ff97188f6b4b1b153dfde3cfec", alias:"open-vehicle"},
        {name: "Растентовка", value: "0x818e8ff1eda1abc349318a478659af08", alias:"canopy"},
    ];

	const context = useContext(AppContext);

	// Берём контекст сохранной формы и подставляем как значение.
	const initialValue = context.form.getFieldValue('typeLoading');


	const [backsideLoading, setbacksideLoading] = useState(initialValue);
	console.log('initValue typeLoading,', initialValue);
    const onRequeirementsTransportChange = (data) => {

		if(data)
		{
			setbacksideLoading(true);
		}

		if(!data)
		{
			setbacksideLoading(false);
		}

        console.log('Requeirements Transport',data);
    }



    return (
        <>
        <Divider>
            Услуги при доставке от адреса
        </Divider>
        <Flex gap="middle" vertical>
            <Form.Item 
                valuePropName="checked"
                name="typeLoading"
                label="Способ отправки">
                
                <Switch
					checkedChildren="Задняя погрузка"
                    unCheckedChildren="Специальные требования к транспорту"
                    onChange={onRequeirementsTransportChange}
                    />

            </Form.Item>
			{ !backsideLoading && (
            <Form.Item name="requirementsTransport"
				label="Специальные требования к транспорту"
				>

				<Checkbox.Group>

					{data.map( (checkbox, idx) => {
						return (
							<Col key={idx}
								span={12}>
								<Checkbox
									key={checkbox.alias}
									value={checkbox.value}
									>
									{checkbox.name}
								</Checkbox>
							</Col>
							)
					})}

				</Checkbox.Group>



			</Form.Item>
			)}
		</Flex>


        </>

    )
}

export default BServicesToShipmentsAddress;