
import React, { useState, useEffect } from 'react';
import { Button, Result } from 'antd';


const ResultUI = () => {
	return(
		<Result
			status="error"
			title="Непредвиденная фатальная ошибка"
			subTitle="Пожалуйста, обратитесь в техническую поддержку предоставив данные из консоли"
			extra={[
			<Button type="primary" key="console">
				Открыть консоль
			</Button>,
			<Button key="buy">Перезагрузить</Button>,
			]}
		/>
		)
}

const ErrorBoundary =({children})=> {
	
	const [hasError, setHasError] = useState(false);
	
	
	useEffect(() => {
		if (hasError) {
			// Log the error or send it to an error tracking service
			console.error('Ошибка обработанная внутри обёртки ErrorBoundary');
		}
	}, [hasError]);
	
	if(hasError)
	{
		return <ResultUI/>
	}
	
	return  children;
	
}


export default ErrorBoundary;